import { sketches } from "../sketches";
import React from "react";
import SketchFrame from "./SketchFrame";
import {
  GAP_SIZE_IN_UNITS,
  MIN_NUM_COLS,
  NUM_COLS,
} from "../galleryDimensions";

export default function Gallery() {
  return (
    <div
      className={`grid-cols-${NUM_COLS} lg:grid-cols-${MIN_NUM_COLS} xl:grid-cols-${NUM_COLS} grid gap-${GAP_SIZE_IN_UNITS} w-full`}
    >
      {sketches.map((sketch) => (
        <SketchFrame sketch={sketch} />
      ))}
    </div>
  );
}
